<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="problem-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="问题详情" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <detail v-if="detail.id" :data="detail"></detail>

    <div class="blank-empty"></div>

    <template slot="blank" v-if="showAction">
      <div class="footer-action" v-if="detail.status == 'wait_assigned' && user.job == 'foreman'">
        <van-button class="action-item-main" color="#108EE9" @click="handleAccept">受理</van-button>
      </div>
      <div class="footer-action" v-if="detail.status == 'following_up' && detail.user && detail.user.id == user.id && detail.canReview">
        <van-button class="action-item-main btn-save" color="rgba(16, 142, 233, 0.15)" @click="handleForward">转发</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleFollow">跟进</van-button>
      </div>

      <div class="footer-action" v-if="detail.canReview && (detail.status == 'pending_review' || detail.status == 'pending_review1' || detail.status == 'pending_review2')">
        <van-button v-if="detail.status == 'wait_assigned' && user.job == 'foreman'" class="action-item-main" color="#108EE9" @click="handleAccept">受理</van-button>
        <van-button class="action-item-main btn-save" color="rgba(16, 142, 233, 0.15)" @click="handleTurnDown">驳回</van-button>
        <van-button v-if="detail.status == 'pending_review' || detail.status == 'pending_review1'" class="action-item-main" color="#108EE9" @click="handlePass">通过</van-button>
        <van-button v-if="detail.status == 'pending_review2'" class="action-item-main" color="#108EE9" @click="handleClose">关闭问题</van-button>
      </div>

      <van-popup 
        v-model="turndownShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          填写驳回原因
        </div>
        <van-field v-model="turndownDesc" placeholder="请填写驳回原因" type="textarea" :rows="2" autosize />
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onTurndownConfirm">确定驳回</van-button>
        </div>
      </van-popup>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import Detail from './Detail'

export default {
  components: {
    Page,
    Detail
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {},
    user: JSON.parse(localStorage.getItem('user')),
    turndownDesc: '',
    turndownShow: false,
    showAction: false
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')

      this.loadDetail()
    },
    onShow () {
      // this.loadDetail()

      if (localStorage.getItem('redirect')) {
        const redirect = localStorage.getItem('redirect')

        localStorage.setItem('redirect', '')

        navigate.open(redirect, {id: this.id})

        this.loadDetail()
      } else {
        this.loadDetail()
      }
    },
    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getProblemDetail({id: this.id}).then(data => {

        this.detail = data
        this.$toast.clear()

        this.showAction = true
      }).catch(msg => {
        this.$toast(msg)
      })
    },
    onClickLeft () {
      navigate.back()
    },

    handleAccept () {
      const _this = this

      this.$dialog.confirm({
        message: '是否确认受理问题？',
        beforeClose (action, done) {
          if (action === 'confirm') {
            console.log(_this.id)
            io.acceptProblem({id: _this.id}).then(() => {
              done()
              _this.loadDetail()
            }).catch(msg => {
              this.$toast(msg)
              done()
            })
          } else {
            done();
          }
        }
      })
    },

    handleForward () {
      navigate.open('organization', {id: this.id, mode: 'problem', job: 'foreman', profession: this.detail.professionId, status: this.detail.status})
    },

    handleFollow () {
      navigate.open('follow', {id: this.id})
    },

    onTurndownConfirm () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })
      this.turndownShow = false
      io.turndownProblem({id: this.id, content: this.turndownDesc}).then(() => {
        this.loadDetail()
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handleTurnDown () {
      const _this = this

      this.turndownShow = true

      // this.$dialog.confirm({
      //   message: '是否确认驳回问题？',
      //   beforeClose (action, done) {
      //     if (action === 'confirm') {
      //       console.log(_this.id)
      //       io.turndownProblem({id: _this.id}).then(() => {
      //         done()
      //         _this.loadDetail()
      //       }).catch(msg => {
      //         this.$toast(msg)
      //         done()
      //       })
      //     } else {
      //       done();
      //     }
      //   }
      // })
    },

    handlePass () {
      if (this.detail.status == 'pending_review') {
        navigate.open('circulation', {id: this.id, flow: JSON.stringify(this.flow),profession: this.detail.professionId, job: 'engineer_duty', status: this.detail.status})
      }

      if (this.detail.status == 'pending_review1') {
        navigate.open('circulation', {id: this.id, flow: JSON.stringify(this.flow),profession: this.detail.professionId, job: 'senior_engineer_business', status: this.detail.status})
      }
    },

    handleClose () {
      const _this = this

      this.$dialog.confirm({
        message: '是否确认关闭问题？',
        beforeClose (action, done) {
          if (action === 'confirm') {
            console.log(_this.id)
            io.closeProblem({id: _this.id}).then(() => {
              done()
              _this.loadDetail()
            }).catch(msg => {
              this.$toast(msg)
              done()
            })
          } else {
            done();
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">

.problem-page{
  .btn-save{
    .van-button__text{
      color: #108EE9;
    }
  }
}

</style>
