<script>
import {problemStatusEnum} from '../../utils/enum.js'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'

export default {
  props: ['data'],
  components: {AutoImage},
  data: () => ({
    problemStatusEnum: problemStatusEnum
  }),
  methods: {
    handleRecord () {
      navigate.open('problemRecord', {id: this.data.id})
    },

    handleFaultDetail () {
      navigate.open('faultdetail', {id: this.data.faultId})
    },

    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.data.images))
      navigate.open('photoViewer', {index: index})
    },

    handlePlayVideo (item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayAnnex (item) {
      navigate.open('browser', {url: item.url})
    }
  }
}
</script>

